var _this = this
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniRate from '@/components/uni-rate/uni-rate.vue'
import uniSection from '@/components/uni-section/uni-section.vue'
export default {
	comments:{
		uniRate,
		uniSection,
		uniNavBar
	},
	data() {
		return {
			indicatorDots: true,
			current:0,
			list:[
				{key:"1",title:"A"},
				{key:"2",title:"B"},
				{key:"3",title:"C"},
				{key:"4",title:"D"},
				{key:"6",title:"E"},
				{key:"7",title:"F"}
			],
			synopsis:false,
		}
	},
	
	created() {
		_this = this,
		_this.getOrder()
	},
	methods: {
		change(e){
			_this.current = e.detail.current
		},
		getOrder(){
			_this.swiperlength,
			_this.list = [
				{key:"1",title:"A"},
				{key:"2",title:"B"},
				{key:"3",title:"C"},
				{key:"4",title:"D"},
				{key:"6",title:"E"},
				{key:"7",title:"F"}
			]
		},
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		home(){
			uni.switchTab({
				url:"/pages/index/index"
			})
		},
	}
}
